    font-family: queen;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  }
  textarea {
    resize: none;
  }
/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
    box-sizing: border-box;
    font-family: queen;
  }
  /*
    2. Remove default margin
  */
  * {
    margin: 0;
  }
  /*
    Typographic tweaks!
    3. Add accessible line-height
    4. Improve text rendering
  */
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  /*
    5. Improve media defaults
  */
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  /*
    6. Remove built-in form typography styles
  */
  input, button, textarea, select {
    font: inherit;
  }
  /*
    7. Avoid text overflows
  */
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  /*
    8. Create a root stacking context
  */
  #root, #__next {
    isolation: isolate;
  }

  body{
    display: grid;
    grid-template-rows: 100px 50px 1fr;
    height: 100vh;
  }
  .todo-title{
    font-size: 70px;
    margin: 10px;
    padding: 10px;
    place-self: center;
  }
  /* .content {
    background-color: rgb(255, 231, 235);
    padding: 20px;
    margin: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-auto-rows: 400px;
    gap: 20px;
    padding: 50px;
  } */
     .content {
    background-color: rgb(255, 231, 235);
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 50px;
  }
.project{
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
  place-items: center;
  overflow-y: auto;
  height: 500px;
  width: 300px;
}
.project-title{
  font-size: 22px;
  border-bottom:  3px solid rgb(0, 0, 0);
  width: 100%;
  padding: 10px;
  position:sticky;
  top: 0;
  background-color: rgb(255, 226, 226);
}
.todo-preview{
  box-shadow: 0 0 0 1px rgb(154, 152, 152);
  max-height: 150px;
  width: 80%;
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 10px;
  font-size: 20px;
}

.container-new-buttons{
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
  font-weight: 900;
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.4);
}
.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.deleteButton, .deleteToDo{
  color: red;
  border-radius: 50%;
  border-style: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 0.75em;
  align-self: self-end;
  margin: 10px;
}

