.high{
  background-color: rgba(241, 165, 165, 0.5);
}
.medium{
  background-color: rgba(255, 255, 167,0.5);
}
.low{
  background-color: rgba(198, 244, 129,0.5);
} 
.todo-preview-title{
 cursor: pointer;
 user-select: none;
 font-weight: 900;
}

.todo-preview-title {
text-align: center;
border: none;
cursor: pointer;
transition: background-color 0.3s ease, transform 0.3s ease;
&&:hover{
    transform: scale(1.3);
  }}

  .container{
    display: flex;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(1px);
    width: 100%;
    height: 100%;
place-items: center;
place-content: center ;
position:fixed;
    top:0;
    left: 0;
  }

  .toDoPage{
    width: 300px;
    height: 600px;
    background-color: rgba(0, 0, 0, 0.7);
    display: grid;
    color: antiquewhite;

  }

  .toDoPage > div,.toDoPage > select{
    border: 0.5px solid #8a8a8a;
    padding: 5px;
    margin: 5px;
    text-align: center;
    overflow-y:auto;
    place-content: center;
    cursor: pointer;
    user-select: none;
    background:transparent;
    color: antiquewhite;
  }

  .toDo-title{
font-weight: 900;
font-size: 25px;
height: 100px;
  }
  .toDoPage {
    position: relative;
    animation-name: animatetop;
    animation-duration: .5s
  }
  
  @keyframes animatetop {
    from {top: -300px; opacity: 0}
    to {top: 0; opacity: 1}
  }
  .toDoPage div input{
    background-color: transparent;
    color: antiquewhite;
  }
  .itemPriority{
    background: transparent;
    color: antiquewhite;
    cursor: pointer;
  }

